.Movie {
    display: flex;
    background-color: whitesmoke;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    text-overflow: ellipsis;
    width: 40%;
    padding: 0 20px;
    margin-bottom: 50px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
}

.Movie__Columns {
    width: 30%;
    box-sizing: border-box;
    text-overflow: ellipsis;
}

.Movie__Columns:last-child {
    width: 60%;
    padding: 20px 0;
}

.Movie .Movie__Poster {
    max-width: 100%;
    position: relative;
    top: -20px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80, 0.22);
}

.Movie h1 {
    font-size: 20px;
    font-weight: 600;
}

.Movie .Movie__Genres {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
}

.Movie__Genres .Movie__Genre {
    margin-right: 10px;
    color: gray;
}

.Movie .Movie__Synopsis {
    text-overflow: ellipsis;
    color: grey;
    overflow: hidden;
}

@media screen and (min-width:320px) and (max-width: 667px){
    .Movie {
        width: 100%;
    }
}

@media screen and (min-width:320px) and (max-width: 667px) and (orientation: portrait){
    .Movie {
        width: 100%;
        flex-direction: column;
    }
    .Movie .Movie__Poster {
        top: 0;
        left: 0;
        width: 100%;
    }
    .Movie .Movie__Columns {
        width: 100% !important;
    }
}

