body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    padding: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    font-size: 14px; 
    /* background-color: rgba(211, 211, 211, 0.856); */
}

.App--loading {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}
.Movie {
    display: -webkit-flex;
    display: flex;
    background-color: whitesmoke;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    text-overflow: ellipsis;
    width: 40%;
    padding: 0 20px;
    margin-bottom: 50px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
}

.Movie__Columns {
    width: 30%;
    box-sizing: border-box;
    text-overflow: ellipsis;
}

.Movie__Columns:last-child {
    width: 60%;
    padding: 20px 0;
}

.Movie .Movie__Poster {
    max-width: 100%;
    position: relative;
    top: -20px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80, 0.22);
}

.Movie h1 {
    font-size: 20px;
    font-weight: 600;
}

.Movie .Movie__Genres {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-bottom: 20px;
}

.Movie__Genres .Movie__Genre {
    margin-right: 10px;
    color: gray;
}

.Movie .Movie__Synopsis {
    text-overflow: ellipsis;
    color: grey;
    overflow: hidden;
}

@media screen and (min-width:320px) and (max-width: 667px){
    .Movie {
        width: 100%;
    }
}

@media screen and (min-width:320px) and (max-width: 667px) and (orientation: portrait){
    .Movie {
        width: 100%;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .Movie .Movie__Poster {
        top: 0;
        left: 0;
        width: 100%;
    }
    .Movie .Movie__Columns {
        width: 100% !important;
    }
}


